/*
    Used to manage styles related to Rock's internal grid component

    1. Bootstrap Overrides
    2. Core Table
    3. Filter
    4. Column Header
    5. Footer
    6. Light Style - Used for small grids
    7. Reusable classes
    8. Column Types
    9. Row Types
    10. Responsive Columns
    11. Components
*/


// 1. Bootstrap Overrides
// -------------------------



.table > tbody > tr > td {
    color: @grid-row-even-color;
}

.table-striped > tbody > tr:nth-child(odd) > td, .table-striped > tbody > tr:nth-child(odd) > th {
  background-color: @grid-row-odd-bg;
  color: @grid-row-odd-color;
}

td.grid-select-cell {
    cursor: pointer;
}

td.wrap-contents { // keep cells from scrolling off screen
    -ms-word-break: break-all;
    word-break: break-all;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
}

.table {
    margin-bottom: 0;
}

.grid {
    margin-bottom: 15px;

    .grid-table {
        border: 1px solid @grid-border-color;
    }

    .grid-table.table-light {
        border: none;
    }
}

.grid.grid-panel {
    .grid-table {
        border: none !important;
    }

    th:first-child,
    td:first-child {
        border-left: 0;
    }

    th:last-child,
    td:last-child {
        border-right: 0;
    }
}

// rwd responsive table override

.grid-panel .table-responsive[data-pattern="priority-columns"],
.table-responsive.table-no-border {
    border: 0;
}

.table-responsive[data-pattern="priority-columns"] {
    position: static; // the default for this is relative, but this causes the scrollbar to always show until you resize the window, then it goes away
}


// 2. Core Table
// -------------------------
.table-wrapper {
    .btn-toolbar {
        display: none;
    }
}


// 3. Filter
// -------------------------
.grid-filter {
    background-color: @grid-filter-bg;
    border-radius: @border-radius-base;
    padding: 6px 12px;
	border-bottom: 1px solid @grid-border-color;
    color: @grid-filter-color;

    header {
        border-radius: @border-radius-base @border-radius-base 0 0;
        padding: 4px;
        cursor: pointer;
        height: 24px;
        text-align: right;

        h3 {
            font-size: 12px;
            margin: 0 2px 0 2px;
            line-height: 16px;
            display: inline;
	        font-weight: @font-weight-light;
        }

        i {
            color: lighten(@grid-filter-color, 20%);
        }

        fieldset {
            margin-bottom: 0;
        }
    }

     h4 {
        margin: 12px 0 20px;
        padding-bottom: 8px;
        font-weight: @font-weight-light;
    }

     .grid-filter-entry {
        margin-bottom: 12px;
    }

     // adjust the background color of pickers in the filters
    .picker .picker-label {
        background-color: @grid-filter-bg;
        color: @grid-filter-color;
        border-color: darken(@grid-filter-bg, 10%)
    }
}


 .grid-filter-entry legend + .control-group {
     margin-top: 0;
 }

.grid-filter-overview {

    h4 {
        margin: 0;
    }

    fieldset {
        margin-bottom: 12px;
    }
}

// 4. Column Header
// -------------------------

.table > thead > tr > th  {
    background-color: @grid-column-header-bg;
	font-size: @grid-column-header-font-size;
	color: @grid-column-header-text-color;
    font-weight: @grid-column-header-font-weight;
    border-color: @grid-column-header-border-color;

    > a:not(.btn) {
        color: @grid-column-header-text-color;
        display: block;
    }

    // sorting
    &.ascending a::before {
        padding-right: 5px;
	    content: "\f0d8";
	    font-family: FontAwesome;
    }

    &.descending a::before {
        padding-right: 5px;
	    content: "\f0d7";
	    font-family: FontAwesome;
    }
}

.table.table-light > thead > tr > th  {
    background-color: transparent;
	color: @text-color;
    border-color: lighten(@text-color, 40%);
}

// 5. Footer
// -------------------------

// pagination
.grid-paging {
    background-color: @grid-pager-bg;
    color: @grid-pager-color;

    .pagination {
        margin-top: 0;
        margin-bottom: 0;

        li {
             > a {
                background-color: @grid-pager-button-bg;
                color: @grid-pager-button-color;
                border-color: @grid-pager-button-border;
            }

            &.active > a {
                background-color: @grid-pager-button-active-bg;
            }
        }


    }

    .grid-pager {
        float: right;
    }

    .grid-pagesize {
        float: left;
    }

    .grid-itemcount {
        float: left;
        font-size: 11px;
        margin-left: 10px;
        margin-top: 7px;
    }
}

// columns
td.grid-col-actions {
    text-align: right;
}

// actions
.grid-actions {
    text-align: right;
    background-color: @grid-footer-bg;

    .btn {
        margin-left: 8px;
        background-color: @grid-footer-button-bg;
        color: @grid-footer-button-text-color;
        border-color: @grid-footer-button-border;
    }
}

// 6. Light Style
// -------------------------

.table-light {
    background-color: transparent;

    thead th {
        background-color: inherit;
        color: @text-color;
        font-weight: @font-weight-bold;
    }

    tfoot td {
        background-color: inherit;

        .grid-actions a {
            border-radius: 0;
            border-style: none;
            border-width: 0;
            box-shadow: none;
            border-color: none;
        }
    }

    .grid-columncommand { // get buttons in the light grid to align over the add in the footer
        text-align: right;
        width: 42px;
        min-width: 42px;
    }
}

// 7. Disabled Styles
// -------------------------

.table-padded {
    td, th {
        padding: 12px !important;
    }
}

// 8. Column Types
// -------------------------

.grid-select-field { // checkbox field
    text-align: center;
    min-width: 40px;

    .checkbox, .radio {

        padding-left: 0;
        margin: 4px 0 0 0;

        input {
            float: none;
            margin: 0;
        }

        &:last-child {
            margin-bottom: 0px;
            margin-left: -12px;
        }

        label {
            padding: 0;
        }
    }
    // future add this: http://webdesign.tutsplus.com/tutorials/quick-tip-easy-css3-checkboxes-and-radio-buttons--webdesign-8953
}

//
.table > tbody > tr > td.grid-select-field {
    padding: 12px 12px 0 12px;
}

.table > thead > tr > th.grid-select-field {
    padding: 14px 12px 10px 12px;
}

.table > thead > tr > th,
.table > tbody > tr > td { // get buttons and text columns to look good together

    padding: 12px;

    &.grid-columncommand {
         padding: 6px;
         min-width: 48px;
    }

    &.grid-columnreorder {
         padding-top: 12px;
         min-width: 48px;
         width:48px;

         a {
             color: lighten(@text-color, 20%);
         }
    }
}

.table .color-field {
    padding: 0;
    width: 4px;

    & + th {
        border-left-width: 0px;
    }
}

.table .grid-icon img {
    width: 72px;
    height: 72px;
}


// 9. Row Types
// -------------------------
.table > tbody > tr.grid-section-header {

    > td {
        background-color: @grid-section-bg;
        color: @grid-section-color;
    }

    &:hover > td {
        background-color: @grid-section-bg !important;
    }

 }

tr.row-highlight {
    td {
        background-color: @grid-hover-bg !important;
        color: @grid-hover-color !important;
    }
}

.table > thead > tr.grid-header-bold {
    th {
        background-color: @grid-column-header-bold-bg;
        color: @grid-column-header-bold-color;
        border-color: lighten(@grid-column-header-bold-bg, 5%);
        vertical-align: top !important;
    }
}

.table > tbody > tr.grid-row-bold {
    td {
        background-color: @grid-column-header-bold-bg;
        color: @grid-column-header-bold-color;
        border-color: lighten(@grid-column-header-bold-bg, 5%);
        vertical-align: top !important;
    }

    &:hover td {
        background-color: lighten(@grid-column-header-bold-bg, 10%);
    }
}

// inactive row
.table > tbody > tr.is-inactive {

    > td {
        color: lighten(@grid-row-even-color, 30%);
    }
 }

.table > tbody > tr.is-inactive-person {

    > td {
        color: lighten(@grid-row-even-color, 30%);
    }
 }

.table > tbody > tr.is-deceased {

    > td {
        color: @brand-danger;
    }
 }

// 10. Responsive Columns
// -------------------------

// unlike Bootstrap's original .table-responsive, this is applied for all widths. (i.e. NOT only applied <768px)
.table-responsive {
    //position: relative;  // commented out to get pop-ups from being trimmed JME 10/8/2015
    width: 100%;
    overflow-y: hidden;
    overflow-x: auto;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    -webkit-overflow-scrolling: touch;
}

// classes to show and hide columns based on their priority
.table-responsive {

    th[data-priority],
    td[data-priority] {
       display: none;
    }

    th[data-priority="1"],
    td[data-priority="1"] {
        display: table-cell;
    }

    @media screen and (min-width: 480px) {
        th[data-priority="2"],
        td[data-priority="2"] {
            display: table-cell;
        }
    }

    @media screen and (min-width: 640px) {
        th[data-priority="3"],
        td[data-priority="3"] {
            display: table-cell;
        }
    }

    @media screen and (min-width: 800px) {
        th[data-priority="4"],
        td[data-priority="4"] {
            display: table-cell;
        }
    }

    @media screen and (min-width: 960px) {
        th[data-priority="5"],
        td[data-priority="5"] {
            display: table-cell;
        }
    }

    @media screen and (min-width: 1120px) {
        th[data-priority="6"],
        td[data-priority="6"] {
            display: table-cell;
        }
    }
}

// 10. Responsive Columns
// -------------------------
.alert.alert-grid {
    margin-top: -15px;
    margin-right: -15px;
    margin-left: -15px;
    border-radius: 0;
    margin-bottom: 15px;
}

// 11. Components
// -------------------------
td .photo-icon {
    opacity: 0.6;
    min-height: 16px;
}